import React, { cloneElement, useCallback, useRef } from 'react';
import { IBaseSplitButtonProps } from './interface';
import { Popover } from '../../../../display/popover';
import { IconArrowDown } from '../../../../basic/icon';
import { ToolbarHoverTip } from '../../toolbarHoverTip';
import { mergeRef, useDefaultProp, mergeClassName } from '../../../../common';
import { Wrapper } from './styled';
import { BadgeDot } from '../../../../basic/badge';

const placementOffset = [4, 0] as [number, number];

export const BaseSplitButton = React.memo(React.forwardRef<HTMLDivElement, IBaseSplitButtonProps>(
  (props, ref) => {
    const {
      icon,
      title,
      tooltip,
      disabled,
      size,
      overlay,
      autoClose,
      active,
      overlayVisible,
      onVisibleChange,
      badgeDot,
      onClick,
      overlayStyle,
      testid,
      role,
      getContainer,
      placement = 'bottomLeft',
      width,
      ...restProps
    } = props;

    const [menuVisible, setMenuVisible] = useDefaultProp(false, overlayVisible, onVisibleChange);

    const cRef = useRef({
      lastTime: 0,
      v: menuVisible,
    });
    cRef.current.v = menuVisible;

    const wrapRef = useRef<HTMLDivElement>(null);

    const onArrowClick = useCallback((e: React.MouseEvent) => {
      const now = Date.now().valueOf();
      if ((now - cRef.current.lastTime) > 300) {
        cRef.current.lastTime = now;
        setMenuVisible(!cRef.current.v);
      }
    }, []);

    const showTitle = size === 'large';

    return (
      <Wrapper
        ref={mergeRef(ref, wrapRef)}
        className={mergeClassName('wdn-toolbar-item', size, active && 'active' , disabled && 'disabled')}
        data-testid={testid}
        data-role={role}
        style={width ? { width: width } : undefined}
      >
        <Popover
          content={overlay}
          onVisibleChange={setMenuVisible}
          visible={menuVisible}
          getContainer={getContainer || (() => wrapRef.current || document.body)}
          placementOffset={placementOffset}
          placement={placement}
          animation
          data-testid={`overlay-${testid}`}
        >
          <div style={{ lineHeight: 0, overflow: 'hidden' }}>
            <ToolbarHoverTip overlayVisible={menuVisible} disabled={disabled} tooltip={tooltip || (!showTitle && !disabled && title)}>
              <div className="wdn-toolbar-split-btn" style={width ? { width: width } : undefined}>
                <div className={mergeClassName('wdn-toolbar-split-left', active && 'active')} onClick={disabled ? undefined : onClick}>
                  {cloneElement(icon, { size: size === 'large' ? 'large' : 'normal' })}
                </div>
                <div className="wdn-toolbar-split-btn-arrow" onClick={onArrowClick}><IconArrowDown className={`wdn-tb-menu-array ${menuVisible ? 'rotate' : ''}`} style={{ width: 10, height: 10 }} /></div>
                {
                  badgeDot && (
                    typeof badgeDot === 'boolean'
                      ? <BadgeDot style={{ position: 'absolute', top: 0, right: 10 }} />
                      : <BadgeDot style={{ position: 'absolute', top: badgeDot.top, right: badgeDot.right + 10 }} />
                  )
                }
              </div>
            </ToolbarHoverTip>
          </div>
        </Popover>
        {showTitle && <div className="wdn-toolbar-title" onClick={disabled ? undefined : onClick}>{title}</div>}
      </Wrapper>
    );
  },
));

